import { ObservableMap, observable, action,toJS} from 'mobx';
const ApiRoot = `https://pokeapi.co/api/v2`


export default class Pokemons {

    @observable page = 0;
    @observable rowsPerPage = 10;
    @observable pokemons = [];
    @observable types = []
    @action setPage = (newPage) => {
        this.page = newPage;
    };
    @action setRowsPerPage = (newPerPage) => {
        this.rowsPerPage = newPerPage;
    };
    @action init = () => {
        Promise.all([this.getTypes(), this.getPockemons()]).then(() => {})
    };

    @action
    getTypes = async () => {
        this.types = await fetch(`${ApiRoot}/type/`);
    };

    @action
    getPockemons = async() => {
        const resp =  await fetch(`${ApiRoot}/pokemon/?offset=0&limit=50`);
        let response = await resp.json();
        this.pokemons= response.results;
    }

}
